import React, { useState } from "react";
import {
  makeStyles,
  Grid,
  Card,
  CardContent,
  Typography,
  Button,
  Avatar,
} from "@material-ui/core";
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import Post2 from "../SendPost/Post2";
const useStyles = makeStyles((theme) => ({
  avatar: {
    width: theme.spacing(5),
  },
  date: {
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(1),
  },
  body: {
    marginTop: theme.spacing(1),
    margin: "auto",
  },
  page:{
    height:'100vh',
    overflow: 'scroll',
    background:'rgba(232, 235, 248, 0.94)'
  },
  card: {
    margin: theme.spacing(1),
  },
  card2: {
    marginLeft: theme.spacing(5),
    marginRight: theme.spacing(5),
    marginTop: theme.spacing(1),
  },
  post: {
    margin: "auto",
  },
}));
function Comment({
  commentData,
  isLogged,
  commentPost,
  commentPostData,
  setcommentPostData,
  handleClose
}) {
  const classes = useStyles();
  return (
   <div className={classes.page}>
      <Grid className={classes.body} item xs="12" md="6">
      <Card className={classes.card}>
        <Button  onClick={handleClose}>
        <HighlightOffIcon/>
        </Button>
        <Typography
          className={classes.date}
          align="right"
          variant="caption"
          display="block"
          gutterBottom
        >
          {commentData.createdAt}
        </Typography>
        <CardContent>
          <Grid container>
            <Grid item xs="2" md="2">
              <Avatar
                className={classes.avatar}
                alt="Remy Sharp"
                src={commentData.photoUrl}
              />
            </Grid>
            <Grid container xs="10" md="10">
              <Grid item xs="12" md="12">
                <Typography variant="h5">{commentData.userName}</Typography>
              </Grid>
              <Grid item xs="12" md="12">
                <Typography variant="subtitle1">
                  {commentData.message}
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
      {isLogged ? (
        <Grid container>
          <Grid className={classes.post} item xs="11" md="11">
            <Post2
              commentPost={commentPost}
              commentPostData={commentPostData}
              setcommentPostData={setcommentPostData}
              postId={commentData.postId}
            />
          </Grid>
        </Grid>
      ) : null}

      {commentData.comments.map((comment) => (
        <Card className={classes.card2}>
          <Typography
            className={classes.date}
            align="right"
            variant="caption"
            display="block"
            gutterBottom
          >
            {comment.createdAt}
          </Typography>
          <CardContent>
            <Grid container>
              <Grid item xs="2" md="2">
                <Avatar
                  className={classes.avatar}
                  alt="Remy Sharp"
                  src={comment.photoUrl}
                />
              </Grid>
              <Grid container xs="10" md="10">
                <Grid item xs="12" md="12">
                  <Typography variant="h5">{comment.userName}</Typography>
                </Grid>
                <Grid item xs="12" md="12">
                  <Typography variant="subtitle1">{comment.message}</Typography>
                </Grid>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      ))}
    </Grid>
   </div>

  );
}
export default Comment;

