import React, { useState } from "react";
import {
  makeStyles,
  Grid,
  Card,
  CardContent,
  Typography,
  Button,
  Avatar,
} from "@material-ui/core";
import moment from 'moment'
const useStyles = makeStyles((theme) => ({
  avatar: {
    width: theme.spacing(5),
  },
  date: {
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(1),
  },
  body: {
    marginTop: theme.spacing(1),
  },
  card: {
    margin: theme.spacing(1),
  },
  card2: {
    marginLeft: theme.spacing(5),
    marginRight: theme.spacing(5),
    marginTop: theme.spacing(1),
  },
}));

const dateFunction = (x) =>{
  const number = parseInt(x)
 const  day = moment(number).format('LLLL')
  return day
}
function Lists({ photoUrl, createdAt, userName, message, like, comments , isLogged , postId , submitComment}) {
  const classes = useStyles();
  const [isCommentActive, setisCommentActive] = useState(false);
  return (
    <Grid className={classes.body} item xs="12" md="12">
      <Card className={classes.card}>
        <Typography
          className={classes.date}
          align="right"
          variant="caption"
          display="block"
          gutterBottom
        >
          {/* {createdAt} */}
          
          {
             dateFunction(createdAt)
          }
        </Typography>
        <CardContent>
          <Grid container>
            <Grid item xs="2" md="2">
              <Avatar
                className={classes.avatar}
                alt="Remy Sharp"
                src={photoUrl}
              />
            </Grid>
            <Grid container xs="10" md="10">
              <Grid item xs="12" md="12">
                <Typography variant="h5">{userName}</Typography>
              </Grid>
              <Grid item xs="12" md="12">
                <Typography variant="subtitle1">{message}</Typography>
              </Grid>
            </Grid>
            <Grid container xs="12" md="12">
              <Grid item xs="6" md="6">
                <Button  onClick={()=>submitComment(postId)} className={classes.button}>Comment</Button>
              </Grid>
              <Grid item xs="6" md="6">
              {isLogged ? <Button  className={classes.button}>Like : {like}</Button> :<Button disabled className={classes.buttonDisable}>Like : {like}</Button>}
              </Grid>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* COMMENTS */}
      {isCommentActive ? (
        <>
          {
          comments.map((comment) => (
            <Card className={classes.card2}>
              <Typography
                className={classes.date}
                align="right"
                variant="caption"
                display="block"
                gutterBottom
              >
                {comment.createdAt}
              </Typography>
              <CardContent>
                <Grid container>
                  <Grid item xs="2" md="2">
                    <Avatar
                      className={classes.avatar}
                      alt="Remy Sharp"
                      src={comment.photoUrl}
                    />
                  </Grid>
                  <Grid container xs="10" md="10">
                    <Grid item xs="12" md="12">
                      <Typography variant="h5">{comment.userName}</Typography>
                    </Grid>
                    <Grid item xs="12" md="12">
                      <Typography variant="subtitle1">
                        {comment.message}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          ))}
        </>
      ) : null}
    </Grid>
  );
}
export default Lists;
