import React from "react";
import {
  Card,
  CardContent,
  Grid,
  TextField,
  Button,
  makeStyles,
} from "@material-ui/core";
import PostAddIcon from "@material-ui/icons/PostAdd";
import SendIcon from "@material-ui/icons/Send";

const useStyles = makeStyles((theme) => ({
  text: {
    display: "flex",
    margin: "auto",
    alignItems: "center",
    justifyContent: "center",
  },
  input: {
    display: "flex",
  },
  button: {
    marginLeft: theme.spacing(5),
  },
  icon: {
    width: theme.spacing(5),
    height: theme.spacing(5),
  },
}));
function Post2({ commentPost, setcommentPostData ,postId}) {
  const classes = useStyles();

  return (
    <>
      <Card>
        <CardContent>
          <Grid className={classes.text} container>
            <Grid item xs="3" md="3">
              <PostAddIcon className={classes.icon} />
            </Grid>
            <Grid item xs="6" md="6">
              <TextField
                onChange={(e) => setcommentPostData(e.target.value)}
                className={classes.input}
                name="addPost"
                id="outlined-basic"
                label="ADD POST"
                variant="outlined"
              />
            </Grid>
            <Grid item xs="3" md="3">
              <Button onClick={()=>commentPost(postId)} className={classes.button}>
                Send <SendIcon />
              </Button>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </>
  );
}
export default Post2;
